/**
 * Firebase Authentication Helper
 * 
 * Provides proper Firebase Google authentication flow
 * Ensures Firebase ID tokens are generated correctly
 */

import { auth, googleProvider } from '@/firebase/client';
import { signInWithPopup, getIdToken } from 'firebase/auth';

/**
 * Authenticate with Google using Firebase
 * Returns Firebase ID token (not Google OAuth token)
 */
export async function authenticateWithGoogle() {
  try {
    // Get Firebase services safely
    const authService = auth();
    const providerService = googleProvider();
    
    if (!authService || !providerService) {
      throw new Error('Firebase Auth not initialized');
    }

    console.log('üîê Starting Firebase Google authentication...');
    
    // Sign in with Google using Firebase
    const result = await signInWithPopup(authService, providerService);
    const user = result.user;
    
    if (!user) {
      throw new Error('No user returned from Google authentication');
    }

    console.log('üîê Google authentication successful, getting Firebase ID token...');
    
    // Get Firebase ID token (this is crucial - not Google OAuth token)
    const idToken = await getIdToken(user, true); // Force refresh to ensure fresh token
    
    console.log('üîê Firebase ID token obtained successfully');
    console.log('üîê Token preview:', idToken.substring(0, 50) + '...');
    
    // Validate token format (Firebase ID tokens are JWT)
    if (!idToken.includes('.')) {
      throw new Error('Invalid Firebase ID token format - not a JWT');
    }

    return {
      user: {
        uid: user.uid,
        email: user.email,
        displayName: user.displayName,
        photoURL: user.photoURL,
        emailVerified: user.emailVerified
      },
      idToken
    };
  } catch (error) {
    console.error('üîê Firebase Google authentication failed:', error);
    throw error;
  }
}

/**
 * Sign out from Firebase
 */
export async function signOutFromFirebase() {
  try {
    const authService = auth();
    if (!authService) {
      throw new Error('Firebase Auth not initialized');
    }

    await authService.signOut();
    console.log('üîê Firebase sign out successful');
  } catch (error) {
    console.error('üîê Firebase sign out failed:', error);
    throw error;
  }
}

/**
 * Get current Firebase user's ID token
 */
export async function getCurrentUserIdToken(forceRefresh = false) {
  try {
    const authService = auth();
    if (!authService?.currentUser) {
      throw new Error('No Firebase user signed in');
    }

    const idToken = await getIdToken(authService.currentUser, forceRefresh);
    console.log('üîê Current user ID token obtained');
    return idToken;
  } catch (error) {
    console.error('üîê Failed to get current user ID token:', error);
    throw error;
  }
}

/**
 * Validate Firebase ID token format
 */
export function validateFirebaseIdToken(token) {
  if (!token || typeof token !== 'string') {
    return false;
  }

  // Firebase ID tokens are JWTs with 3 parts separated by dots
  const parts = token.split('.');
  if (parts.length !== 3) {
    return false;
  }

  try {
    // Try to decode the payload (second part)
    const payload = JSON.parse(atob(parts[1].replace(/-/g, '+').replace(/_/g, '/')));
    
    // Firebase ID tokens should have these claims
    return (
      payload.iss && 
      payload.aud && 
      payload.exp && 
      payload.iat &&
      payload.sub &&
      payload.iss.includes('securetoken.google.com')
    );
  } catch {
    return false;
  }
}
